.AllScheduleMain {
  top: 109px;
  margin: 20px;
  border-radius: 4px;
  overflow: visible;
  padding: 14px 20px;
  width: 1086px;
  margin: 20px auto;
  background: #fff;
  border-radius: 4px;

  .FilterArea {
    display: flex;
    .date-card {
      min-width: 250px;
      margin-right: 10px;
      margin-bottom: 20px;
      .legend-wrapper {
        width: 50px;
      }
    }
    .JobDropDown {
      min-width: 330px;
      margin-right: 10px;
      margin-bottom: 20px;
      [class$="-ValueContainer"] {
        position: unset;
        height: 100%;
      }
      [class$="-indicatorContainer"] {
        padding: 2px !important;
        color: #333 !important;
      }
    }
  }
  .tableInvite {
    color: #21745d;
    cursor: pointer;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    // img {
    //   -webkit-backface-visibility: hidden;
    //   -moz-backface-visibility: hidden;
    //   -ms-backface-visibility: hidden;
    // }
    .value {
      text-align: right;
      margin: 0 0 0 8px;
      //   width: 15px;
    }
  }
  .ViewMessage {
    color: #21745d;
    cursor: pointer;
    text-align: right;
  }
}
.messageModalSchedule {
  .messageDiv {
    outline: none;
    position: absolute;
    width: 50%;
    max-height: 50%;
    overflow: auto;
    left: calc(100% - 75%);
    top: calc(100% - 75%);
    background-color: #fff;
    border-radius: 8px;
    padding: 20px;
  }
}
